<template>
  <div>
    <el-row style="background:#e3ebec;">
      <el-row style="margin-top:15px;margin-bottom:20px;">
        <el-col :span="4">
          <div class="grid-content"></div>
        </el-col>
        <el-col :span="4">（暂定是ps一个图标）</el-col>
      </el-row>
      <el-divider></el-divider>
    </el-row>

    <el-row>
      <el-col :span="15">
        <el-col :span="7">
          <div class="grid-content"></div>
        </el-col>

        <el-col :span="17">

          <el-card :body-style="{ padding: '0px' }">
            <div style="padding: 20px;">

              <div style="font-size:26px;margin-bottom:10px;color:#333333;font-weight:bold;text-align:left">
                <span>
                    {{o.name}}
                </span>
              </div>

              <div style="font-size:14px;margin-bottom:10px;">
                <div style="font-size:14px;margin-bottom:10px;color:#333333;text-align:left">
                <span >
                    {{o.short}}
                </span>
                <el-divider>
                </el-divider>
                </div>

                <el-col>
                    <img :src=o.url class="image">
                </el-col>

              </div>
            </div>
          </el-card>

            <el-col>
              这里就从后端拿所有的文字评论出来
            </el-col>
<!--            <el-col>-->
<!--               <pld v-bind:ob=list2 v-bind:nowID=this.pageid />-->
<!--            </el-col>-->
<!--            <el-col>-->
<!--               <pld v-bind:ob=list2 v-bind:nowID=this.pageid />-->
<!--            </el-col>-->
<!--            <el-col>-->
<!--               <pld v-bind:ob=list2 v-bind:nowID=this.pageid />-->
<!--            </el-col>-->
          <el-col v-for="(s,i) in lists" :key="i">
            <pld v-bind:ob=s v-bind:nowID=o.hpno />
          </el-col>
        </el-col>

      </el-col>

      <el-col style="margin-top:30px;">
          <topiccom v-bind:nowID=o.hpno v-bind:urid="this.urid">
          </topiccom>
      </el-col>

    </el-row>
  </div>
</template>

<script>
import topiccom from '@/components/topiccom.vue'
import pld from '@/components/pld.vue'
export default {
  name: "topicd",
  components:{
        topiccom,
        pld,
},
  props:{
    o:{
      hpno:1,
      url:'',
      name:'one',
      short:'one',
    },
  },
  data() {
    return {
      lists:[
        {id:'cnm',titl:'cnmb',deta:"cnll"},
        {id:'cnm',titl:'cnmb',deta:"cnll"},
        {id:'cnm',titl:'cnmb',deta:"cnll"},
      ],
      keyword: "",
      pageid:250,
      hd:{
          titl:'我是标题',
          de:'我是详情'
      },
      dialogImageUrl: '',
      dialogVisible: false,
      formLabelWidth: '80px',
      limitNum: 3,
      form: {},

      list2:
      {
        id:'我是用户的id',
        deta:'我是用户写的废话',
        titl:'我是用户起的标题',
        up_num:1,
        down_num:10
      }
    };
  },
  methods: {
      search() {
        console.log(this.keyword);
      },
    },
  // props:{
  //   o:Object
  // }
  created() {
    var guro=localStorage.getItem('username');
    var gurt=sessionStorage.getItem('username');
    if(guro!=null) this.urid=guro;
    if(gurt!=null) this.urid=gurt;
    let params={op:2,hpno:this.o.hpno};
    this.$http.post('/gethp.php',params).then(
            response=>{
              this.lists=[];
              var pllist=response.data
              var i;
              for(i=0;i<pllist.length;i++){
                var s1=pllist[i][0];
                var s2=pllist[i][1];
                var s3=pllist[i][2];
                var s4=pllist[i][3];
                var s5=pllist[i][4];
                var tmp={id:s1,titl:s2,deta:s3,up_num:s4,down_num:s5};
                this.lists.push(tmp);
              }
            }
    )
  }
};
</script>

<style scoped>
