<template>
  <div>
      <div class="input">
        <el-row class="titl">
            <el-col>
                <el-input
                type="textarea"
                :rows="1"
                placeholder="标题不能空着哦"
                v-model="textarea2">
                </el-input>
            </el-col>
        </el-row>


        <el-row>
          <el-col :span="18">

            <el-input
                type="textarea"
                :rows="5"
                placeholder="请自觉遵守互联网相关的政策法规，严禁发布色情、暴力、反动的言论。"
                v-model="textarea1">
                </el-input>
        </el-col>



            <el-col :span="3">
                    <el-button type="primary"  class="com" @click="sub_com1">
                        提交评论
                    </el-button>
            </el-col>

             <el-col :span="3">
                 <div class="grid-content">
                 </div>
             </el-col>
        </el-row>
      </div>
  </div>
</template>

<script>
export default {
    name:'topiccom',
    data()
    {
        return{
            textarea1:'',
            textarea2:'',
           }
    },
    methods:
    {
        /*提交到后端数据库*/
        sub_com1()
        {
            var s2=this.urid;
            //alert(s2);
            var s3=this.textarea2;
            var s4=this.textarea1;
            if(s2=='') alert("请先登录！");
            else {
                let params={op:3,hpno:this.nowID,id:s2,titl:s3,deta:s4};
                this.$http.post('/gethp.php',params).then(
                    reponse=>{
                        alert(reponse.data)
                    }
                )
            }
        },
        created() {
        var guro=localStorage.getItem('username');
        var gurt=sessionStorage.getItem('username');
        if(guro!=null) this.urid=guro;
        if(gurt!=null) this.urid=gurt;
    }

    },
    props:{
         nowID:{type:Number,default:0},
        urid:{type:String,default:'root'},
    },

    // /*生命周期函数，从数据库里拿评论渲染，这个应该写在具体的页面里?*/
    // mounted() {
    //     alert(this.textarea1);
    // }
}

</script>

<style scoped>
.com
{
    height: 100px;
    width: 100px;
}

.grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .titl
  {
      margin-bottom: 10px;
      width: 200px;
  }
</style>
